<template>
    <a-modal
            width="60%"
            v-model="visible"
            :footer="null"
            :destroyOnClose="true"
    >
        <div slot="title">
            <a-row>
                <a-col :span="1">
                    <img :src="require('@/assets/pc/images/dhico.png')"
                         style="z-index: 9999;height: 18px">
                </a-col>
                <a-col :span="23">
                    {{title}}
                </a-col>
            </a-row>
        </div>

        <a-spin :spinning="spinning">
            <div v-html="user_excerpt"></div>
            <div style="height: 10px"></div>
            <a-list
                    :split="false"
                    :dataSource="party_relation"
            >
                <a-list-item slot="renderItem"
                             slot-scope="item, index">
                    <span style="color: #c12625;font-weight:bold;padding-right: 5px">·</span> {{item.title}} {{item.info}}
                </a-list-item>
            </a-list>
        </a-spin>


    </a-modal>
</template>

<script>
    import {getUserWithPartyInfo} from "@/api/user";
    // TODO 需要透明的icon
    export default {
        props: {
            id: {required: true, type: Number},
            visible: {required: true, type: Boolean},
        },
        data() {
            return {
                spinning: true,
                title: '数据加载中。。',
                confirmLoading: false,
                user_data: {},
            }
        },
        computed: {
            user_excerpt() {
                if (!this.user_data.hasOwnProperty('user_excerpt')) {
                    return null;
                }
                return this.user_data.user_excerpt;
            },
            party_relation() {
                if (!this.user_data.hasOwnProperty('party_relation')) {
                    return [];
                }
                let party_relation = this.user_data.party_relation;
                console.log(party_relation);

                let result = [];
                for (let i = 0; i < party_relation.length; i++) {
                    let row = party_relation[i];
                    result.push({
                        title:row.started_at_format + '-' + row.ended_at_format,
                        info:  row.department_name
                    });
                }
                return result;
            }
        },
        methods: {
            fetch() {
                this.spinning = true;
                if (this.id === 0) {
                    return;
                }

                getUserWithPartyInfo(this.id)
                    .then(res => {
                        this.title = res.data.name;
                        this.user_data = res.data;
                        console.log(this.user_data);
                        this.spinning = false;
                    })
            },
        },
        watch: {
            visible(newVal, oldVal) {
                if (newVal) {
                    this.fetch();
                } else {
                    this.title = '数据加载中。。';
                    this.user_data = {};
                    this.$emit('update:visible', false);
                }
            }
        }
    }
</script>

