<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <CheckElection :election="election"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../选举列表/components/TopButtons'
    import CheckElection from './components/Check'
    import {getAttendElection} from '@/api/election'

    export default {
        components: {
            TopButtons, CheckElection
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        data() {
            return {
                election: {}
            }
        },
        methods: {
            fetch() {
                getAttendElection(this.thisId)
                    .then(res => {
                        this.election = res.data;
                    })
                    .catch(res=>{
                        history.go(-1)
                    })
            }
        },
        created() {
            this.fetch();
        }
    }
</script>
