<template>
    <div class="columns">
        <div class="xwsh_nr">
            <div class="pad-30">
                <h1>{{election.title}}</h1>
                <h2>{{election.started_at_format}}</h2>

                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <div v-html="initiate_content"></div>
                    </a-col>
                    <a-col :span="22">
                        <downloadFiles v-if="files.length" :list="files"/>
                    </a-col>
                </a-row>

                <a-row type="flex" justify="center">
                    <a-col :span="22">
                        <options :id="thisId" :data="optionsData" :selectCount="optionsSelectCount"/>
                    </a-col>
                </a-row>
            </div>
        </div>
    </div>
</template>

<script>
    import downloadFiles from '@/components/download/Files';
    import Options from './Options';

    export default {
        components: {downloadFiles, Options},
        props: {
            election: {type: Object}
        },
        computed: {
            files() {
                if (!this.election.hasOwnProperty('more')) {
                    return [];
                }
                if (!this.election.more.hasOwnProperty('files')) {
                    return [];
                }
                return Array.isArray(this.election.more.files) ? this.election.more.files : [];
            },
            thisId() {
                if (!this.election.hasOwnProperty('id')) {
                    return 0;
                }
                return this.election.id;
            },
            optionsData() {
                if (!this.election.hasOwnProperty('options')) {
                    return [];
                }
                if (!this.election.options) {
                    return [];
                }
                return this.election.options;
            },
            optionsSelectCount() {
                if (!this.election.hasOwnProperty('option_select_count')) {
                    return 0;
                }

                return this.election.option_select_count;
            },
            initiate_content() {
                if (!this.election.hasOwnProperty('initiate_content')) {
                    return null;
                }
                return this.election.initiate_content;
            },
            publicity_content() {
                if (!this.election.hasOwnProperty('publicity_content')) {
                    return null;
                }
                return this.election.publicity_content;
            },
        },
        methods: {},
    }
</script>
