/**
 * @param {string,array} path  routeName路径。如果为字符串的话以"."分割。
 * @description 通过路径获取深层的menus
 */
export const getMenus = (path) => {
    let arr;
    if ((typeof path === 'object') && path.constructor === Array) {
        arr = path;
    } else if (typeof path === 'string') {
        arr = path.split(".");
    } else {
        throw  'getMenus path type error';
    }

    if (!arr.length) {
        return [];
    }

    let level = 0;
    let dict = pc;

    while (true) {
        if (!arr.hasOwnProperty(level)) {
            return [];
        }

        let isFind = false;
        for (let i = 0; i < dict.length; i++) {
            if (!dict[i].hasOwnProperty('routeName')) {
                continue;
            }

            if (dict[i].routeName === arr[level] && dict[i].hasOwnProperty('menus')) {
                dict = dict[i].menus;
                level++;
                isFind = true;

                if (level === arr.length) {
                    return dict;
                } else {
                    break;
                }
            }
        }

        if (!isFind) {
            return [];
        }
    }
};

/**
 * @param {string} routeName
 * @param {array,null} dict
 * @description   通过routeName寻找对应的auth
 */
const getAuth = (routeName, dict = pc) => {
    for (let i = 0; i < dict.length; i++) {
        let row = dict[i];
        if (!row.hasOwnProperty('routeName')) {
            continue;
        }

        if (row.routeName === routeName) {
            if (row.hasOwnProperty('auth')) {
                return row.auth;
            } else {
                return null;
            }
        }

        if (row.hasOwnProperty('menus')) {
            let getFartherAuth = getAuth(routeName, row.menus);
            if (getFartherAuth) {
                return getFartherAuth;
            }
        }
    }
};
