<template>
    <div>
        <p style="color: #af1a17;font-weight: bold;">
            被选举人
            <template v-if="selectCount>0">
                <span style="font-size: 12px;font-weight:normal">可选择人数：{{selectCount}}人</span>
            </template>
        </p>
        <a-row>
            <a-col
                    v-for="(row,index) in data" :span="12" style="width: 155px">
                <a-card
                        hoverable
                        style="width: 150px"
                >
                    <div
                            style="width: 100%;height: 185px;" slot="cover"
                            @click="handleCheck(row.content_user.id)">
                        <img
                                style="width: 100%;height: 185px;"
                                :src="getUrl(row.content_user.thumbnail_format)"/>
                    </div>
                    <a-card-meta>
                        <template slot="title">
                            <a-checkbox v-model="checkResult[index]">
                                {{row.content_user.name}}
                            </a-checkbox>
                        </template>
                    </a-card-meta>
                </a-card>
            </a-col>
        </a-row>
        <UserDetailModal :id="userDetailId" :visible.sync="visible"/>
        <div style="height: 10px"></div>
        <a-button type="primary" @click="handleSubmit">提交</a-button>
    </div>
</template>

<script>
    import {getImageUrl} from "@/libs/tools";
    import UserDetailModal from '@/components/user/check-modal/Index';
    import {attendSubmit} from '@/api/election';

    export default {
        name: "Options",
        components: {UserDetailModal},
        props: {
            id: {required: true, type: Number},
            data: {required: true, type: Array},
            selectCount: {required: true, type: Number}
        },
        data() {
            return {
                userDetailId: 0,
                visible: false,
                checkResult: [],
            }
        },
        methods: {
            getUrl(url) {
                return getImageUrl(url);
            },
            handleCheck(id) {
                this.userDetailId = id;
                if (this.visible) {
                    this.visible = false;
                    this.visible = true;
                } else {
                    this.visible = true;
                }
            },
            handleSubmit() {
                let result = this.checkResult;
                if (!result.length) {
                    this.$message.warning('请进行选择');
                    return false;
                }

                let res = [];
                for (let i = 0; i < result.length; i++) {
                    if (result[i] === true) {
                        res.push(this.data[i].id);
                    }
                }

                if (!res.length) {
                    this.$message.warning('请进行选择');
                    return false;
                }

                if (res.length > this.selectCount) {
                    this.$message.warning('选择人数多于 ' + this.selectCount + ' 人，请重新选择');
                    return false;
                }

                attendSubmit(this.id, res)
                    .then(res => {
                        this.$message.success('投票成功');

                        console.log(res);
                    })
            }
        },
    }
</script>
